<script>
	import { onMount } from "svelte";
	import { quill } from "svelte-quill";

	let options = {
		placeholder: "Write something from outside...",
		modules: {
			toolbar: {
				container: [
					[{ header: [1, 2, false] }],
					["bold", "italic", "underline"],
					["code-block", "image", "image-url"],
				],
				handlers: {
					"image-url": function () {
						const url = prompt("Enter the image URL");
						if (url) {
							const range = this.quill.getSelection();
							this.quill.insertEmbed(range.index, "image", url);
						}
					},
				},
			},
		},
	};

	let content = { html: "", text: "" };

	onMount(() => {
		// insert content  to a class name image-url
		const image_url = document.querySelector(".ql-image-url");
		image_url.style = "font-size: 14px;";
		image_url.innerHTML = `<div style="width: 80px;" >
			   <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="18" height="18">
					<path d="M0 0h24v24H0z" fill="none"/>
					<path d="M21 19V5a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2zm-2 0H5V5h14v14zM8 12l2.5 3.01L14 10l4 5H6l2-3z"/>
				</svg>
			</div>`;
	
	});
</script>

<svelte:head>
	<link href="//cdn.quilljs.com/1.3.6/quill.snow.css" rel="stylesheet" />
</svelte:head>

<main>
	<h1>SVELTE-QUILL</h1>
	<div
		class="editor"
		use:quill={options}
		on:text-change={(e) => (content = e.detail)}
	/>

	<br />
	Resulting HTML:

	{@html content.html}
</main>
